<template>
  <div class="page">
    <Header>
      <template #left>
        <button @click="callback" class="header__button header__button--back">返回</button>
      </template>
      <template #right>
        <a class="header__button link" href="javascript:void (0);">查看规则</a>
      </template>
    </Header>
    <h1>超级合伙人</h1>
    <a @click="$router.push('/test')" href="javascript:void (0);">无效果页</a>
  </div>
</template>

<script>
  import Header from '../components/Header'

  export default {
    name: 'referralPartner',
    components: {
      Header
    },
    beforeRouteEnter(to, from, next) {
      next(vm => {
        vm.formPath = from.path
      })
    },
    data() {
      return {
        formPath: null
      }
    },
    created() {
      console.log('referralPartner')
    },
    methods: {
      callback() {
        if (!this.formPath || this.formPath === '/') {
          this.$router.replace('/')
        } else {
          this.$router.back()
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .page {
    text-align: center;
  }

  h1 {
    margin-top: $--header-height;
  }

  .link {
    font-size: rem(14);
    color: #cca352;
  }
</style>
